import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
/**
 * Generated class for the InicioPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-inicio',
  templateUrl: 'inicio.html',
})
export class InicioPage {


idUs:integer;
  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, public modalCtrl : ModalController, private alertCtrl: AlertController) {

   

  }



	  public openModal(){
    var data = { message : 'hello world' };
    var modalPage = this.modalCtrl.create('LoginPage',data);
    modalPage.present();
		}



    presentAlert(titulo,mensaje) {
    let alert = this.alertCtrl.create({
      title: titulo,
      subTitle: mensaje,
      buttons: ['Cerrar'],
         cssClass: 'alertCustomCss'
    });
    alert.present();
  }

    solicitarRutinaApi() {
    console.log(this.idUs);


    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();
 
  
    this.apiProvider.solicitarRR(this.idUs)
      .then(data => {
    
     if(data.data){
     this.presentAlert('Rutina solicitada','Excelente! Tener un plan de ejercicios es el primer paso para cumplir tus metas! Un instructor te asignara la rutina en breve');
     }
     else{
      this.presentAlert('Error','Ha ocurrido un error inesperado');
     }

            loading.dismissAll();
      });






  }

        solicitarRutina(){

          this.apiProvider.verificarLogin()
    .then(data => {    
      console.log(data);
      if(data){


    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();

    this.apiProvider.verificarLogin()
    .then(data => {    
       this.idUs=data.idUsuario;

      this.apiProvider.getRutinaUsuario(data.idUsuario)
      .then(data => {
      
      if(data[1].length>0){

      
      this.presentAlert('Atencion!', 'Tu rutina ha sido asiganda, ve a la seccion de rutina y empieza tu plan de entrenamiento!');
       loading.dismissAll();

      }

      else{
      this.solicitarRutinaApi();


      }

      


      });



     });

        


      }
      else{
        this.openModal();
      }


     });
    }





  getDataHome() {
    return this.apiProvider.getDataHome()
    .then(data => {
      
      console.log(data);
      console.log(data[1][0]);

	  this.proximaClase = data[0][0];
      this.dia = data[1][0];
      
      
    });
  }



	  ionViewDidLoad() {
	    console.log('f4');

	    let loading = this.loadingController.create({content : "cargando..."});
	    loading.present();
	    this.getDataHome().then(()=>{
	        loading.dismissAll();
	    });
	  }

 	goClase(tipo){
  if(tipo == 1){
    this.navCtrl.push('ClasePage', this.proximaClase);
  }
    if(tipo == 2){
    this.navCtrl.push('ClasePage', this.dia);
  }

  }

  goActividad(){
  this.navCtrl.push('ActividadPage', this.dia);
  }





}



